// Logos

.slide-studycover__logo {
	width: 10rem;
	margin-bottom: $base-spacing;
	@include media($up-to-small) {
		margin-bottom: $small-spacing/2;
	}
}

// Shoreditch Works logo

$sw-logo-width: 12%;

.sw-logo {
	width: $sw-logo-width;
	height: auto;
	position: absolute;
	left: (100% - $sw-logo-width)*0.5;
	top: (100% - $sw-logo-width)*0.45;
	@include media($up-to-large) {
		width: $sw-logo-width*1.5;
		left: (100% - $sw-logo-width*1.5)*0.5;
	}
	@include media($up-to-small) {
		width: $sw-logo-width*2.5;
		left: (100% - $sw-logo-width*2.5)*0.5;
	}
}

// Penrose logo

$penrose-logo-width: 30%;
$penrose-logo-ratio: 0.13;

.penrose-logo {
	width: $penrose-logo-width;
	height: auto;
	margin-top: $base-spacing*2;
}

// client logos

$logo-number: 4;
$gutter-width: 10%;

.client-logos {
	margin-top: 8%;
	@include media($up-to-small) {
		display: none;
	}
	@include media($up-to-large) {
		margin-top: 25%;	
	}
	li {
		height: 0;
		width: (100% - ($logo-number - 1) * $gutter-width) / $logo-number;
		padding-bottom: (100% - ($logo-number - 1) * $gutter-width) / $logo-number;
		margin-right: $gutter-width;
		display: block;
		float: left;
		background-color: rgba(0,0,0,0.15);
		text-align: center;
		position: relative;
		img {

		}
		&:last-child {
			margin-right: 0;
		}
		// split to rows of two (not being used)
		@include media($up-to-small) {
			width: (100% - $gutter-width) / 2;
			padding-bottom: (100% - $gutter-width) / 2;
			margin-right: $gutter-width;
			margin-bottom: $gutter-width;
			&:nth-child(even){
				margin-right: 0;
			}
		}
	}
}

.logo-ctrlio, .logo-decoded {
	width: 60%;
	margin-top: 42%;
}

.logo-webwewant {
	width: 40%;
	margin-top: 25%;
}

.logo-ilga {
	width: 30%;
	margin-top: 32%;
}