// Contact page

.contact-bg {
	background-image: url(../images/contact-bg.jpg);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;	
}

.contact-slide {
	text-align: center;
}

.contact-slide__header {
	margin-top: 20%;
	font-size: $heading-font-size * 1.5;
	line-height: $heading-line-height * 1.5;
	padding: 0 20%;
	margin-bottom: $small-spacing;
	@include media($up-to-small) {
		padding: 0%;
		font-size: $mobile-header-font-size;
		line-height: $mobile-header-line-height;
		margin-bottom: $base-spacing;
	}
}

.contact-slide__contact {
	font-family: $light-font-family;
	font-size: $base-font-size * 1.333;
	line-height: $base-line-height * 1.333;
	margin-bottom: $base-spacing * 2;
	a {
		color: $dark-bg-text-colour;
		padding-bottom: $small-spacing/3;
		border-bottom: 1px solid transparent;
		@include transition(all 0.25s ease-in-out);
	}
	a:hover {
		color: $dark-bg-text-colour;
		border-bottom: 1px solid $dark-bg-text-colour;
	}
	@include media($up-to-small) {
		font-size: $base-font-size;
	}
}

.contact-slide__hero {
	@include display(flex);
  @include align-items(center);
  position: relative;
  height: 100%;
  &>div {
  	position: relative;
  	width: 100%;
  	top: -$base-spacing*3;
  	@include media($up-to-small) {
			top: 0;
  	}
  }
}

.social-icons__twitter, 
.social-icons__linkedin {
	img {
		max-width: $base-spacing*2;
		max-height: $base-spacing*1.5;
		@include transition(transform 0.15s ease-out);
		&:hover {
			@include transform(scale(1.2));
		}
	}
}

.social-icons__twitter {
	margin-right: $base-spacing*2;
}