// Slide up/down buttons

$next-button-size : 60px;

.next-slide-button {
	padding: 0;
	background-color: $colourWhite;
	border: none;
	font-size: 0px;
	width: $next-button-size;
	height: $next-button-size;
	/* background-image: url(../images/icon-arrow-down-black.svg);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 25%; */
	position: fixed;
	z-index: 9;
	left: calc(50% - (#{$next-button-size}/2));
	bottom: $site-border-width*0.95;
	margin-bottom: 0;
	outline: none;
	border-radius: 0;
}

body.last-slide .next-slide-button {
	display: none;
}

.next-slide-button {
	@include animation(button-reveal 2s ease-out);
}

@include keyframes(button-reveal) {
  0% {
    @include transform(translateY($next-button-size/2));
    opacity: 0;
  }
  60% {
  	@include transform(translateY($next-button-size/2));
    opacity: 0;
  }
  100% {
    @include transform(translateY(0));
    opacity: 1;
  }
}

.prev-slide-button {
	@include transition(all 0.25s ease-in-out);
	padding: 0;
	position: fixed;
	font-size: 0px;
	background-color: transparent;
	height: calc(#{$site-border-width} + #{$next-button-size});
	width: 80%;
	left: 10%;
	top: 0;
	opacity: 0;
	z-index: 2;
	border: none;
	outline: none;
	cursor: default;
	&:after {
		top: $site-border-width*0.95; //to account for rounding errors
		background-color: white;
		position: absolute;
		content: '';
		width: $next-button-size;
		height: $next-button-size;
		left: calc(50% - (#{$next-button-size} /2));
		/* background-image: url(../images/icon-arrow-down-black.svg);
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 25%; */
		cursor: pointer;
	}
	&:hover {
		opacity: 1;
	}
	i.arrow.up {
		width: $next-button-size;
		height: $next-button-size;
		top: $site-border-width;
		margin-top: -36px; // not sure why we need this to pull the icon up a bit
		z-index: 2;
		&:before,
		&:after {
			top: 33%;
		}
	}
}

body.first-slide .prev-slide-button {
	display: none;
}

.prev-slide-button, .next-slide-button {
	@include media($up-to-large) {
		display: none;
	}
}