
// final page of study

.slide-studyfinal {
	@include media($up-to-small) {
		.content-frame {
			display: block;
		}
		form {
			margin-top: $base-spacing*2;
			position: relative;
			display: block;
			button {
				display: block;
				width: calc(100% - #{$base-spacing}*3.5);
				margin-bottom: 0;
			}
		}
	}
}