.cover-bg {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center top;
}

.slide-studycover--made {
	@extend .cover-bg;
	background-position: center center;
	color: $light-bg-text-colour;
	background-color: #F2E259;
	background-image: url(../images/made-study-cover.jpg);
}

.slide-studycover--tlobf {
	@extend .cover-bg;
	background-image: url(../images/tlobf-cover.jpg);
	color: $dark-bg-text-colour;
	background-color: rgba(0,0,0,0.1);
	@include media($up-to-large) {
		background-position: right center;
	}
	@include media($up-to-small) {
		background-position: right 20% center;
	}
}

.moire-background {
	background-image: url(../images/selected-moire.jpg);
	background-position: center center;
	background-repeat: no-repeat;
	background-size: 40%;
	@include media($up-to-small) {
		background-size: 100%;
	}
	@include media($up-to-large) {
		background-size: 70%;
	}
}
.font-background {
	background-image: url(../images/selected-hackney-dark.jpg);
	background-position: center center;
	background-repeat: no-repeat;
	background-size: 80%;
	@include media($up-to-large) {
		background-size: 70%;
	}
	@include media($up-to-small) {
		background-size: 200%;
	}
}

.photo-bg {
	background-image: url(../images/tree.jpg);
	@extend .cover-bg;
}

.sw-bg {
	background-image: url(../images/sw-bg.jpg);
	@extend .cover-bg;
	background-position: center;
}

.penrose-bg {
	background-image: url(../images/penrose-corporate.jpg);
	@extend .cover-bg;
	background-position: center;
}

.axe-bg {
	background-image: url(../images/axe.jpg);
	@extend .cover-bg;
}

.container-bg {
	background-image: url(../images/containers.jpg);
	@extend .cover-bg;
}

.path-bg {
	background-image: url(../images/path.jpg);
	@extend .cover-bg;
}