.bullet-list {
	position: absolute;
	margin-bottom: 0;
	bottom: $header-offset;
	font-weight: $heading-font-weight;
	li {
		padding-left: $base-spacing;
		&:before {
			content: '';
			background-image: url(../images/icon-tick.svg);
			background-repeat: no-repeat;
			background-position: middle middle;
			width: $base-spacing*0.5;
			height: $base-spacing*0.5;
			position: absolute;
			margin-left: -$small-spacing*1.25;
			margin-top: $base-spacing*0.25;
		}
	}
	@include media($up-to-large) {
		font-weight: $base-font-weight;
	}
	@include media($up-to-small) {
		font-size: $small-font-size;
		line-height: $small-line-height;
		li {
			padding-bottom: $base-spacing*0.25;
		}
	}
}