// menu system

// motion blur for white text moving left to right
@keyframes motionblur {
  0% 		{ text-shadow: -10px 0 5px rgba(255,255,255,1); }
  100% 	{ text-shadow: 0 0 0 rgba(255,255,255,0); }
}

// calculated from px to avoid rounding errors
$hamburger-box-size: 80px;
$hamburger-stripe-width: 25px; 
$hamburger-stripe-height: 2px;
$hamburger-stripe-gap: 9px;
$hamburger-tablet-multiplier: 0.8;
$hamburger-mobile-multiplier: 0.7;
$hamburger-left-position: ($hamburger-box-size - $hamburger-stripe-width) / 2;
$hamburger-top-line: ($hamburger-box-size - (($hamburger-stripe-height*3) + ($hamburger-stripe-gap*2))) / 2;
$hamburger-middle-line: ( $hamburger-box-size / 2) - ($hamburger-stripe-height/2);
$hamburger-bottom-line: (($hamburger-box-size - (($hamburger-stripe-height*3) + ($hamburger-stripe-gap*2))) / 2) + ($hamburger-stripe-height*2) + ($hamburger-stripe-gap*2);
.menu-button, 
a.menu-button,
a.menu-button:active,
a.menu-button:hover,
a.menu-button:visited {
	border: none;
	outline: none;
	position: relative;
	width: $hamburger-box-size;
	height: $hamburger-box-size;
	//background-color: $colourBrightRed;
	background-color: $colourBlack;
	text-align: center;
	padding-top: 5px;
	color: $colourWhite;
	z-index: 11;
	position: fixed;
	top: 0;
	right: 0;
	@include media($up-to-large) {
		width: $hamburger-box-size*$hamburger-tablet-multiplier;
		height: $hamburger-box-size*$hamburger-tablet-multiplier;
	}
	@include media($up-to-small) {
		width: $hamburger-box-size*$hamburger-mobile-multiplier;
		height: $hamburger-box-size*$hamburger-mobile-multiplier;
	}
	span {
		display: block;
	  position: absolute;
	  height: $hamburger-stripe-height;
	  width: $hamburger-stripe-width;
	  background: white;
	  opacity: 1;
	  left: $hamburger-left-position;
	  @include transform(rotate(0deg));
		@include transition(all 0.25s ease-in-out);
		@include media($up-to-large) {
			left: (($hamburger-box-size * $hamburger-tablet-multiplier) - $hamburger-stripe-width) / 2;
			margin-top: -($hamburger-top-line - (($hamburger-box-size*$hamburger-tablet-multiplier - (($hamburger-stripe-height*3) + ($hamburger-stripe-gap*2))) / 2));
		}
		@include media($up-to-small) {
			left: (($hamburger-box-size * $hamburger-mobile-multiplier) - $hamburger-stripe-width) / 2;
			margin-top: -($hamburger-top-line - (($hamburger-box-size*$hamburger-mobile-multiplier - (($hamburger-stripe-height*3) + ($hamburger-stripe-gap*2))) / 2));
		}
	}
	span:nth-child(1) {
	  top: $hamburger-top-line;
	}
	span:nth-child(2), span:nth-child(3) {
	  top: $hamburger-middle-line;
	}
	span:nth-child(4) {
	  top: $hamburger-bottom-line;
	}
	@include transition(transform 0.5s ease-in);
	&:hover {
		@include transition(transform 0.15s ease-out);
		span:nth-child(1) {
		  @include transform(translateY(-2px));
		}
		span:nth-child(4) {
		  @include transform(translateY(2px));
		}
	}
}

body.menu-open .menu-button {
	span:nth-child(1), span:nth-child(4) {
	  top: ($hamburger-box-size/2) - ($hamburger-stripe-height/2);
	  width: 0%;
	  left: $hamburger-box-size/2;
	  opacity: 0;
	}
	span:nth-child(2) {
	  @include transform(rotate(45deg));
	}
	span:nth-child(3) {
	  @include transform(rotate(-45deg));
	}
}

.menu-wrapper {
	@include transform(translateX(-100%));
	@include transition(all 0.3s ease-in);
	width: 100%;
	height: 100%;
	background-color: rgba(255, 41, 0, 0.9); // transparent red
	// background-color: rgba(0, 0, 0, 0.9); // transparent black
	// background-color: rgba(255, 255, 255, 0.9); // transparent white
	background-image: url(../images/noise-texture.png);
	background-repeat: repeat;
	position: fixed;
	z-index: 10;
	top: 0;
	left: 0;
	opacity: 0.1;
}

body.menu-open {
	.menu-wrapper {
		@include transform(translateX(0%));	
		opacity: 1;
	}
	.menu-link-list {
		@include transform(translateX(40%));	
		opacity: 1;
		-webkit-animation: motionblur 0.8s ease-out;
  	animation: motionblur 0.8s ease-out;
  	@include media($up-to-small) {
  		@include transform(translateX(0%));	
		}
		@include media($up-to-large) {
  		@include transform(translateX(0%));	
		}
	}
	a.menu-button {
		background-color: black;
	}
}

.menu-link-list {
	@include transition(all 0.6s ease-out);
	@include transform(translateX(-100%));	
	width: 40%;
	font-size: $base-font-size * 1.5;
	line-height: $base-line-height * 1.5;
	// font-weight: $light-font-weight;
	font-weight: $heading-font-weight;
	//letter-spacing: $loose-font-spacing;
	position: relative;
	top: $hamburger-box-size;
	position: fixed;
	left: 20%;
	opacity: 0;
	//text-transform: uppercase;
	//border-top: 1px solid rgba(255,255,255,0.5);
	@include media($up-to-large) {
		font-size: $base-font-size * 1.2;
		line-height: $base-line-height * 1.2;
		width: 100%;
		padding: 10%;
		left: 0;
		top: 0;
	}
	@include media($up-to-small) {
		font-size: $base-font-size;
		line-height: $base-line-height;
		width: 100%;
		padding: 5%;
		left: 0;
		top: 0;
	}
}

.menu-link-item a {
	position: relative;
	display: inline-block;
	padding: $small-spacing 4% $small-spacing 3%;
	border-top: 1px solid transparent;
	border-bottom: 1px solid transparent;
	@include transition(border 1s ease-in);
	margin-bottom: -1px;
	@include media($up-to-small) {
	//	border-bottom: 1px solid rgba(255,255,255,0.5);	
	padding: $small-spacing*0.75 4% $small-spacing*0.75 3%;
	}
	&:after {
		@include transition(all 0.5s ease-in-out);
		content: '';
		opacity: 0.9;
		position: absolute;
		background-color: $colourWhite;
		width: $base-spacing/2;
		height: $base-spacing/8;
		bottom: 0;
		left: 0;
	}
}
.menu-link-item:first-child a {
	@include media($up-to-small) {
	//	border-top: 1px solid rgba(255,255,255,0.5);	
	}
}

.menu-link-item a:hover {
	//border-top: 1px solid rgba(255,255,255,0.5);
	//border-bottom: 1px solid rgba(255,255,255,0.5);
	@include transition(border 0.15s ease-out);
	&:after {
		@include transition(all 0.05s ease-in);
		content: '';
		width: 100%;
		//opacity: 0.3;
		//height: 2px;
	}
}

.menu-link-item a, 
.menu-link-item a:visited,
.menu-link-item a:hover,
.menu-link-item a:active {
	color: #fff;
}

