// CSS arrows adapted from http://codepen.io/firya/pen/jqaig

$arrow_width: 100%;
$arrow_height: 100%;
$line_width: 20px;
$line_thickness: 1px;
$arrow_angle: 45deg;
$arrow_angle_hover: 35deg;
$arrow_angle_active: 25deg;

i.arrow {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  backface-visibility: hidden;
  
  &:before,
  &:after {
    content: "";
    display: block;
    border-radius: 10px;
    position: absolute;
    background: #000;
    @include transition(all 0.1s ease-in-out);
  }
    
  &:before {
  	@include transform(rotate(- #{$arrow_angle}));
  }
  
  &:after {
    @include transform(rotate(#{$arrow_angle}));
  }
  
  &.left, 
  &.right {
    width: $arrow_width;
    height: $arrow_height;
    
    &:before,
    &:after {
      width: $line_width;
      height: $line_thickness;
      top: 50%;
      left: 0;
    }
  }
  
  &.left {
    &:before,
    &:after {
      @include transform-origin(0% 50%);
    }
  }
  
  &.right {
    &:before,
    &:after {
      @include transform-origin(100% 50%);
    }
  }
  
  &.up, 
  &.down {
    width: $arrow_height;
    height: $arrow_width;
    
    &:before,
    &:after {
      height: $line_width;
      width: $line_thickness;
      top: 25%;
      left: 50%;
    }
  }
  
  &.up {
    &:before,
    &:after {
      @include transform-origin(50% 0%);
    }
  }
  
  &.down {
    &:before,
    &:after {
      @include transform-origin(50% 100%);
    }
  }
  
  &:hover {
    &:before {
      @include transform(rotate(- #{$arrow_angle_hover}));
    }
    &:after {
      @include transform(rotate(#{$arrow_angle_hover}));
    }
  }
  
  &:active {
    &:before {
      @include transform(rotate(- #{$arrow_angle_active}));
    }
    &:after {
      @include transform(rotate(#{$arrow_angle_active}));
    }
  }
}