// Typographic styles
body {
	@include font-feature-settings("liga");
	text-rendering: optimizeLegibility;
}

// responsive type scales

$mobile-study-header-font-size : $heading-font-size*0.8;
$mobile-study-header-line-height : $heading-line-height*0.8;
$mobile-header-font-size : $heading-font-size;
$mobile-header-line-height : $heading-line-height;
$tablet-header-font-size : $heading-font-size * 1.2;
$tablet-header-line-height : $heading-line-height * 1.2;
$tablet-study-header-font-size : $heading-font-size*0.9;
$tablet-study-header-line-height : $heading-line-height*0.9;

// V3 type

$v3-text-only-header-font-size: $base-font-size*2.5;
$v3-text-only-header-line-height: $v3-text-only-header-font-size * 1.333;

// Running headers

.running-header {
	font-family: $light-font-family;
	padding-right: 10%;
	font-size: $small-font-size;
	letter-spacing: $loose-font-spacing;
	display: block;
	position: relative;
	text-transform: uppercase;
	margin-bottom: $small-spacing*3;
	z-index: 2;
	&:after {
		content: "";
		width: $header-offset/2;
		bottom: -$small-spacing;
		left: 0;
		border-bottom: 1px solid $dark-bg-text-colour;
		position: absolute;
	}
	@include media($up-to-large) {
		font-size: $small-font-size*0.9;
		line-height: $small-font-size;
		margin-top: $small-spacing*0.2;
		margin-bottom: $small-spacing*2;
	}
	@include media($up-to-small) {
		font-size: $small-font-size*0.8;
		line-height: $small-font-size;
		margin-top: $small-spacing*0.2;
	}
}
.slide-light-bg .running-header {
	&:after {
		border-bottom-color: $light-bg-text-colour; 
	}
}

// Study page headers

.slide-studypage__header {
	padding-right: 40%;
	font-size: $heading-font-size * 1.2;
	line-height: $heading-line-height * 1.2;
	@include media($up-to-large) {
		padding-right: 20%;
	}
	@include media($up-to-small) {
		width: 100%;
		font-size: $mobile-study-header-font-size;
		line-height: $mobile-study-header-line-height;
		margin-top: $small-spacing;
	}
}

// Study cover headers

.slide-studycover__header {
	padding-right: 48%;
	font-size: $heading-font-size * 1.2;
	line-height: $heading-line-height * 1.2;
	@include media($up-to-large) {
		padding-right: 20%;
	}
	@include media($up-to-small) {
		width: 100%;
		font-size: $mobile-study-header-font-size;
		line-height: $mobile-study-header-line-height;
	}
}

// Study cover subtitles

.slide-studycover__subtitle {
	font-size: $small-font-size;
	line-height: $small-line-height;
	margin-bottom: 0;
	position: absolute;
	width: 30%;
	bottom: $header-offset;
	@include media($up-to-small) {
		width: 90%;
	}
}

.slide-studypage__header--wide {
	@extend .slide-studypage__header;
	padding-right: 0%;
	margin-bottom: 0;
}

// Text-only slide headers

.slide-text-only__header {
	width: 70%;
	padding-right: 10%;
	margin-bottom: 0;
	font-size: $v3-text-only-header-font-size;
	line-height: $v3-text-only-header-line-height;
	@include media($up-to-large) {
		width: 100%;
		font-size: $v3-text-only-header-font-size*0.7;
		line-height: $v3-text-only-header-line-height*0.7;
	}
	@include media($up-to-small) {
		font-size: $v3-text-only-header-font-size*0.5;
		line-height: $v3-text-only-header-line-height*0.5;
	}
}


