// Generic device image styles - uses a lot of magic numbers to get the positions right

.slide-studypage__device-image {
	max-width: 80%;
	max-height: 95%;
	display: block;
	margin: auto;
	margin-top: $small-spacing;
}

.device-images {
	height: 85%;
	position: relative;
	img {
		@include transition(transform 1s ease-in);
	}
	img.slide-studypage__laptop-image:hover,
	img.slide-studypage__tablet-image:hover,
	img.slide-studypage__mobile-image:hover {
		@include transition(transform 0.15s ease-out);
		transform: scale(1.05);
	}
	>div {
		position: relative
	}
	@include display(flex);
  @include align-items(center);
  @include media($up-to-small) {
  	width: 90%;
    margin: auto;
    display: block;
  	@include align-items(none);
  }
}

.slide-studypage__tablet-image {
	position: absolute;
	max-width: 18.5vw;
	left: 8.9vw;
	top: 1.5vw;
	border-radius: 1.5vw;
  box-shadow: 0px 0px 40px -8px rgba(0,0,0,0.3);
  @include media($up-to-small) {
		width: 40%;
    max-width: 40%;
    position: relative;
    left: inherit;
    left: 14%;
    border-radius: 3vw;
	}
}

.slide-studypage__laptop-image {
	//opacity: 0.05;
	position: absolute;
	max-width: 55.4vw;
	left: 23.8vw;
	top: -2.4vw;
	@include media($up-to-small) {
		width: 100%;
    max-width: 100%;
    position: relative;
    left: inherit;
    top: inherit;
	}
}

.slide-studypage__laptop-screen-shadow {
  position: absolute;
	max-width: 57.2vw;
	left: 22.9vw;
	top: -3.9vw;
  // border-radius: 1.5vw;
	// box-shadow: 0 0 100px -8px rgba(0,0,0,.4);
	@include media($up-to-small) {
		display: none;
	}
	@include media($widescreen) {
		display: none;
	}
}

.slide-studypage__mobile-image {
	position: absolute;
	max-width: 11.3vw;
	top: 14.1vw;
	left: 20.3vw;
	border-radius: 1.5vw;
  box-shadow: 0px 0px 40px -8px rgba(0,0,0,0.3);
  @include media($up-to-small) {
		width: 27%;
    max-width: 27%;
    position: relative;
    left: inherit;
    border-radius: 3vw;
    left: 17%;
    top: 1%;
	}
}