// Split background styles

.slide-split-bg {
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 50%;
	left: 0;
	background-repeat: no-repeat;
	background-size: cover;
	background-color: $light-bg-text-colour;
	text-align: center;
	@include media($up-to-small) {
		height: 45%;
	}
}

.slide-split-bg__img {
	max-height: 70%;
	max-width: 80%;
	margin: auto;
	top: 7%;
	position: relative;
	@include media($up-to-small) {
		max-height: 90%;
	}
}