// Styles for MADE study

.slide-bg-gradient--made {
	@include linear-gradient(left, #E1BD3C 0%, #FBEC61 100%)
}

.slide-studycover--made .slide-studycover__logo {
	width: 15rem;
	@include media($up-to-small) {
		width: 30%;
		margin-top: $hamburger-box-size*$hamburger-mobile-multiplier;
	}
}

.slide-split-bg--made-iterations {
	@extend .slide-split-bg;
	background-image: url(../images/made-iterations.jpg);	
}

.slide-split-bg--made-ia {
	@extend .slide-split-bg;
	background-image: url(../images/wireframing.jpg);	
}

