// Styles for TLOBF study

.slide-bg-gradient--tlobf {
	@include linear-gradient(left, #CA0D4C 0%, #E30641 100%)
}

.slide-studycover--tlobf {
	.v3-slide-studycover__header {
		margin-top: 13%;
	}
}

.slide-studycover--tlobf .slide-studycover__logo {
	width: 30rem;
	@include media($up-to-small) {
		width: 100%;
		margin-top: $hamburger-box-size*$hamburger-mobile-multiplier;
	}
}

.slide-split-bg--tlobf-process {
	@extend .slide-split-bg;
	background-image: url(../images/tlobf-process.png);	
	background-size: 85%;
	background-position: center top;
	@include media($up-to-small) {
		background-size: 330%;
	  background-position: 25% top;
	}
}