// Buttons

form {
	display: block;
}

button, .button {
	font-family: $button-font-family;
	font-size: $small-font-size;
	background: transparent;	
	border-style: solid;
	padding:  0 $small-spacing;
	height: $small-spacing*3;
}

.button--rightbox { // ie. arrow box on the right
	padding-right: $small-spacing*4;
	position: relative;
	&:after {
		content: "";
		height: 100%;
		width: $small-spacing*3;
		background-color: $dark-bg-text-colour;
		position: absolute;
		top: 0;
		right: 0;
	}
}

.button--leftbox { // ie. arrow box on the left
	padding-left: $small-spacing*4;
	position: relative;
	&:before {
		content: "";
		height: 100%;
		width: $small-spacing*3;
		background-color: $light-bg-text-colour;
		position: absolute;
		top: 0;
		left: 0;
	}
}

.button--light  {
	border-color: $dark-bg-text-colour;
	&:after {
		background-color: $dark-bg-text-colour;
		background-image: url(../images/icon-arrow-down-black.svg);
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 32%;
	}
	&:before {
		background-color: $dark-bg-text-colour;
		background-image: url(../images/icon-arrow-down-black.svg);
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 32%;
	}
}

.button--dark  {
	border-color: $light-bg-text-colour;
	&:after {
		background-color: $light-bg-text-colour;
		background-image: url(../images/icon-arrow-down-white.svg);
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 30%;
	}
	&:before {
		background-color: $light-bg-text-colour;
		background-image: url(../images/icon-arrow-down-white.svg);
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 30%;
	}
}

.button--right {
	&:after {
		background-image: url(../images/icon-arrow-right-white.svg);
		background-size: 36%;
	}
	&:before {
		background-image: url(../images/icon-arrow-right-white.svg);
		background-size: 36%;
	}
	&.button--light {
		&:after {
		background-image: url(../images/icon-arrow-right-black.svg);
		background-size: 36%;
	}
		&:before {
			background-image: url(../images/icon-arrow-right-black.svg);
			background-size: 36%;
		}	
	}
}

.button--topright {
	&:after {
		background-image: url(../images/icon-arrow-topright-white.svg);
		background-size: 27%;
	}
	&:before {
		background-image: url(../images/icon-arrow-topright-white.svg);
		background-size: 27%;
	}
	&.button--light {
		&:after {
		background-image: url(../images/icon-arrow-topright-black.svg);
		background-size: 27%;
		}
		&:before {
			background-image: url(../images/icon-arrow-topright-black.svg);
			background-size: 27%;
		}
	}
}

.button-group, .button-group--right, .button-group--left {
	bottom: $base-spacing*3;
	position: absolute;
	width: calc(100% - #{$header-offset} * 2);
	text-align: center;
	button {
		margin-bottom: 0;
		min-width: 10rem;
		font-size: $small-font-size;
	}
	button:first-child {
		margin-right: $base-spacing*1.5;
	}
	@include media($up-to-small) {
		bottom: $base-spacing*1.9;
		button:first-child {
			margin-right: 0;
			margin-bottom: -2px;
		}
	}
}

.button-group--left {
	left: $header-offset;
	width: auto;
	@include media($up-to-small) {
		width: calc(100% - #{$header-offset} * 2);
	}
}

.button-group--right {
	@include media($up-to-small) {
	}
	@include media($small-plus) {
		right: $header-offset;
		width: auto;
	}
}