// Home

.slide-home {
	text-align: center;
	color: $dark-bg-text-colour;
}

.slide-home__hero {
	@include display(flex);
  @include align-items(center);
  height: 100%;
  position: relative;
  div {
		top: -$monogram-width*0.5;
		position: relative;
		width: 100%;
  }
}


.home__monogram {
	width: $monogram-width;
	height: auto;
	position: relative;
	margin-top: $header-offset/2;
	margin-bottom: $header-offset/2;
	@include media($up-to-large) {
		width: $monogram-width*1.2;
		margin-bottom: $header-offset/4;
	}	
	@include media($up-to-small) {
		width: $monogram-width*2;
		margin-bottom: $header-offset;
	}	
}

.slide-home__header {
	font-size: $heading-font-size * 1.5;
	line-height: $heading-line-height * 1.5;
	padding: 0 20%;
	margin-bottom: $header-offset/2;
	@include media($up-to-large) {
		padding: 10%;
		font-size: $tablet-header-font-size;
		line-height: $tablet-header-line-height;
	}
	@include media($up-to-small) {
		padding: 0%;
		font-size: $mobile-header-font-size;
		line-height: $mobile-header-line-height;
	}
}