// Slide defaults

body {
	background-color: #222;
	background-image: url(../images/jb.jpg);
	background-size: cover;
	background-position: center center;
	background-attachment: fixed;
	background-repeat: no-repeat;
	border: $site-border-width solid white;
	@include media($up-to-small) {
		border: none;
	}
}

.swiper-container {
    width: 100%;
    height: 100%;
}

.content-frame {
	height: calc(100% - 2*#{$header-offset});
	padding: $header-offset;
	overflow: hidden;
}

.slide-dark-bg {
	color: $dark-bg-text-colour;	
	background-color: $light-bg-text-colour;	
}

.slide-light-bg {
	color: $light-bg-text-colour;	
	background-color: yellow;
}

.v3-slide-vertical-centre {
	.content-frame {
		@include align-items(center);
  	@include display(flex);
	}
}